import { mapState } from "vuex";
import {
  addFavoriteService,
  queryArticlesService,
  queryFeedArticlesService,
  queryTagsService,
  unFavoriteService,
} from "./service";
export default {
  name: "Home",
  async asyncData() {
    const limit = 20;
    const page = 1;
    const offset = (page - 1) * limit;
    const [articlesRes, tagRes] = await Promise.all([
      queryArticlesService({
        limit,
        offset,
      }),
      queryTagsService(),
    ]);
    const { data } = articlesRes;
    const { data: tagData } = tagRes;
    return {
      articles: data.articles,
      articlesCount: data.articlesCount,
      limit,
      offset,
      page,
      tags: tagData.tags,
    };
  },
  data() {
    return {
      query: {
        tag: "", // 标签参数
      },
      currentActiveTab: 1,
      currentArticle: null,
    };
  },
  computed: {
    total() {
      return Math.ceil(this.articlesCount / this.limit);
    },
    ...mapState(["user"]),
  },
  methods: {
    async changePage(number) {
      const methodName =
        this.currentActiveTab === 0
          ? queryFeedArticlesService
          : queryArticlesService;
      this.page = number;
      const { data } = await methodName({
        limit: this.limit,
        offset: (number - 1) * this.limit,
        ...this.query,
      });
      this.articles = data.articles;
      this.articlesCount = data.articlesCount;
      if (this.query.tag) {
        this.currentActiveTab = 2;
      }
    },
    queryArticles(tag) {
      this.query.tag = tag;
      this.changePage(1);
    },
    queryFeedArticles() {
      this.query.tag = "";
      this.changePage(1);
    },
    async toggleFavorite(article) {
      this.currentArticle = article.slug;
      if (!article.favorited) {
        await addFavoriteService(article.slug);
        article.favoritesCount += 1;
      } else {
        await unFavoriteService(article.slug);
        article.favoritesCount -= 1;
      }
      article.favorited = !article.favorited
      this.currentArticle = null;
    }
  },
};
